import Item from "../models/Item"

const initialState = [
  new Item({
    id: 1,
    name: "",
    antiMonsterCondition: (monster) => (
      monster.atack <= 3
    ),
    description: "3"
  }),
  new Item({
    id: 2,
    name: "",
    antiMonsterCondition: (monster) => (
      monster.isTypeUndead()
    ),
    description: ""
  }),
  new Item({
    id: 3,
    name: "",
    defense: 3,
    description: "HP +3"
  }),
  new Item({
    id: 4,
    name: "",
    defense: 5,
    description: "HP +5"
  }),
  new Item({
    id: 5,
    name: "",
    antiMonsterCondition: (monster) => (
      monster.isTypeDragon()
    ),
    description: ""
  }),
  new Item({
    id: 6,
    name: "",
    description: ""
  })
]

const item = (item, action) => {
  switch (action.type) {
  case "TOGGLE_EQUIPMENT":
    if (item.id !== action.id) {
      return item
    }

    return item.toggleEquipment()
  case "CHANGE_VORPAL_SWORD_TARGET":
    if (item.name !== "") {
      return item
    }

    return item.setAntiMonsterCondition((monster) => (
      monster.name === action.monsterName
    ))
  default:
    return item
  }
}

const items = (state = initialState, action) => {
  switch (action.type) {
  case "TOGGLE_EQUIPMENT":
    return state.map(i => item(i, action))
  case "CHANGE_VORPAL_SWORD_TARGET":
    return state.map(i => item(i, action))
  default:
    return state
  }
}

export default items


