import Monster from "../models/Monster"

const initialState = [
  new Monster({ id: 1, name: "", atack: 1}),
  new Monster({ id: 2, name: "", atack: 1}),
  new Monster({ id: 3, name: "", atack: 2, type: "undead"}),
  new Monster({ id: 4, name: "", atack: 2, type: "undead"}),
  new Monster({ id: 5, name: "", atack: 3}),
  new Monster({ id: 6, name: "", atack: 3}),
  new Monster({ id: 7, name: "", atack: 4, type: "undead"}),
  new Monster({ id: 8, name: "", atack: 4, type: "undead"}),
  new Monster({ id: 9, name: "", atack: 5}),
  new Monster({ id: 10, name: "", atack: 5}),
  new Monster({ id: 11, name: "", atack: 5, type: "undead"}),
  new Monster({ id: 12, name: "", atack: 7}),
  new Monster({ id: 13, name: "", atack: 9, type: "dragon"}),
]

const monster = (monster, action) => {
  switch (action.type) {
  case "TOGGLE_JOIN_MONSTER":
    if (monster.id !== action.id) {
      return monster
    }

    return monster.toggleJoined()
    
  default:
    return monster
  }
}

const monsters = (state = initialState, action) => {
  switch (action.type) {
  case "TOGGLE_JOIN_MONSTER":
    return state.map(t =>
        monster(t, action)
      )
  default:
    return state
  }
}

export default monsters


